"use client";

import { useEffect, useState } from 'react';
import { userService, User } from '@/features/users/services/user.service';
import CreateUserModal from '@/components/users/CreateUserModal';
import { useToast } from '@/context/ToastContext';
import { authService } from '@/features/auth/services/auth.service';
import { useRouter } from 'next/navigation';

export default function UsersPage() {
    const router = useRouter();
    const { showToast } = useToast();
    const [users, setUsers] = useState<User[]>([]);
    const [loading, setLoading] = useState(true);
    const [isCreateModalOpen, setIsCreateModalOpen] = useState(false);
    const [currentUser, setCurrentUser] = useState<any>(null);

    useEffect(() => {
        const user = authService.getCurrentUser();
        if (!user || user.role !== 'SUPERADMIN') {
            showToast('No tienes permiso para ver esta p치gina', 'error');
            router.push('/dashboard');
            return;
        }
        setCurrentUser(user);
        loadUsers();
    }, [router, showToast]);

    const loadUsers = async () => {
        setLoading(true);
        try {
            const data = await userService.getAll();
            setUsers(data);
        } catch (error) {
            console.error(error);
            showToast('Error al cargar usuarios', 'error');
        } finally {
            setLoading(false);
        }
    };

    const handleToggleStatus = async (id: string, currentStatus: boolean) => {
        try {
            await userService.toggleStatus(id);
            setUsers(users.map(u => u.id === id ? { ...u, isActive: !currentStatus } : u));
            showToast('Estado de usuario actualizado', 'success');
        } catch (error) {
            showToast('Error al actualizar estado', 'error');
        }
    };

    const roleColors: Record<string, string> = {
        SUPERADMIN: 'bg-yellow-100 text-yellow-800 border-yellow-200',
        ADMIN: 'bg-red-100 text-red-800 border-red-200',
        TEACHER: 'bg-blue-100 text-blue-800 border-blue-200',
        STUDENT: 'bg-gray-100 text-gray-800 border-gray-200',
    };

    return (
        <div className="min-h-screen bg-gray-50 p-6">
            <div className="max-w-7xl mx-auto">
                <div className="flex justify-between items-center mb-6">
                    <div>
                        <h1 className="text-2xl font-bold text-gray-800">Gesti칩n de Usuarios</h1>
                        <p className="text-sm text-gray-500">Administra el acceso y roles del sistema.</p>
                    </div>
                    <button 
                        onClick={() => setIsCreateModalOpen(true)}
                        className="bg-upds-main text-white px-4 py-2 rounded-lg hover:bg-blue-900 transition-colors shadow-md flex items-center gap-2"
                    >
                        <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 4v16m8-8H4" /></svg>
                        Nuevo Usuario
                    </button>
                    <button 
                         onClick={() => router.push('/dashboard')}
                         className="ml-4 text-gray-500 hover:text-upds-main underline text-sm"
                    >
                        Volver al Dashboard
                    </button>
                </div>

                <div className="bg-white rounded-xl shadow-sm border border-gray-200 overflow-hidden">
                    <table className="min-w-full divide-y divide-gray-200">
                        <thead className="bg-gray-50">
                            <tr>
                                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Usuario</th>
                                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Rol</th>
                                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Estado</th>
                                <th scope="col" className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">Creado</th>
                                <th scope="col" className="relative px-6 py-3"><span className="sr-only">Acciones</span></th>
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {users.map((user) => (
                                <tr key={user.id} className="hover:bg-gray-50 transition-colors">
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        <div className="flex items-center">
                                            <div className="h-10 w-10 flex-shrink-0">
                                                <div className="h-10 w-10 rounded-full bg-upds-light flex items-center justify-center text-white font-bold shadow-sm">
                                                    {user.fullName.charAt(0)}
                                                </div>
                                            </div>
                                            <div className="ml-4">
                                                <div className="text-sm font-medium text-gray-900">{user.fullName}</div>
                                                <div className="text-sm text-gray-500">{user.username}</div>
                                                {user.email && <div className="text-xs text-gray-400">{user.email}</div>}
                                            </div>
                                        </div>
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        <span className={`px-2 inline-flex text-xs leading-5 font-semibold rounded-full border ${roleColors[user.role] || 'bg-gray-100 text-gray-800'}`}>
                                            {user.role}
                                        </span>
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap">
                                        {user.isActive ? (
                                            <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">Activo</span>
                                        ) : (
                                            <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-red-100 text-red-800">Inactivo</span>
                                        )}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                                        {new Date(user.createdAt).toLocaleDateString()}
                                    </td>
                                    <td className="px-6 py-4 whitespace-nowrap text-right text-sm font-medium">
                                        <button 
                                            onClick={() => handleToggleStatus(user.id, user.isActive)}
                                            disabled={user.username === 'superadmin'} // Protecci칩n mock
                                            className={`${user.isActive ? 'text-red-600 hover:text-red-900' : 'text-green-600 hover:text-green-900'} font-semibold disabled:opacity-30 disabled:cursor-not-allowed`}
                                        >
                                            {user.isActive ? 'Desactivar' : 'Activar'}
                                        </button>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>

            <CreateUserModal 
                isOpen={isCreateModalOpen} 
                onClose={() => setIsCreateModalOpen(false)}
                onUserCreated={loadUsers}
            />
        </div>
    );
}
