"use client";

import { useState } from 'react';
import { userService } from '@/features/users/services/user.service';
import { useToast } from '@/context/ToastContext';

interface CreateUserModalProps {
    isOpen: boolean;
    onClose: () => void;
    onUserCreated: () => void;
}

export default function CreateUserModal({ isOpen, onClose, onUserCreated }: CreateUserModalProps) {
    const { showToast } = useToast();
    const [formData, setFormData] = useState({
        fullName: '',
        username: '',
        password: '',
        email: '',
        role: 'TEACHER'
    });
    const [loading, setLoading] = useState(false);

    if (!isOpen) return null;

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setLoading(true);
        try {
            await userService.create(formData);
            showToast('Usuario creado exitosamente', 'success');
            onUserCreated();
            setFormData({ fullName: '', username: '', password: '', email: '', role: 'TEACHER' }); // Reset
            onClose();
        } catch (error: any) {
            console.error(error);
            showToast(error.response?.data?.error || 'Error al crear usuario', 'error');
        } finally {
            setLoading(false);
        }
    };

    return (
        <div className="fixed inset-0 z-40 overflow-y-auto">
            {/* Backdrop */}
            <div className="fixed inset-0 bg-black bg-opacity-50 transition-opacity" onClick={onClose}></div>

            {/* Modal */}
            <div className="flex min-h-full items-center justify-center p-4">
                <div className="relative transform overflow-hidden rounded-lg bg-white text-left shadow-xl transition-all sm:w-full sm:max-w-lg">
                    <div className="bg-white px-4 pb-4 pt-5 sm:p-6 sm:pb-4">
                        <div className="sm:flex sm:items-start">
                           <div className="mt-3 text-center sm:ml-4 sm:mt-0 sm:text-left w-full">
                                <h3 className="text-lg font-semibold leading-6 text-gray-900 mb-4">Nuevo Usuario</h3>
                                <form onSubmit={handleSubmit} className="space-y-4">
                                    <div>
                                        <label className="block text-sm font-medium text-gray-700">Nombre Completo</label>
                                        <input 
                                            type="text" required
                                            className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-upds-main focus:ring focus:ring-upds-main focus:ring-opacity-50 border p-2 text-sm"
                                            value={formData.fullName}
                                            onChange={e => setFormData({...formData, fullName: e.target.value})}
                                        />
                                    </div>
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700">Usuario</label>
                                            <input 
                                                type="text" required
                                                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-upds-main focus:ring focus:ring-upds-main focus:ring-opacity-50 border p-2 text-sm"
                                                value={formData.username}
                                                onChange={e => setFormData({...formData, username: e.target.value})}
                                            />
                                        </div>
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700">Email (Opcional)</label>
                                            <input 
                                                type="email" 
                                                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-upds-main focus:ring focus:ring-upds-main focus:ring-opacity-50 border p-2 text-sm"
                                                value={formData.email}
                                                onChange={e => setFormData({...formData, email: e.target.value})}
                                            />
                                        </div>
                                    </div>
                                    <div className="grid grid-cols-2 gap-4">
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700">Contrase침a</label>
                                            <input 
                                                type="text" required // Text para ver what typeamos al crear
                                                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-upds-main focus:ring focus:ring-upds-main focus:ring-opacity-50 border p-2 text-sm"
                                                value={formData.password}
                                                onChange={e => setFormData({...formData, password: e.target.value})}
                                            />
                                        </div>
                                        <div>
                                            <label className="block text-sm font-medium text-gray-700">Rol</label>
                                            <select 
                                                className="mt-1 block w-full rounded-md border-gray-300 shadow-sm focus:border-upds-main focus:ring focus:ring-upds-main focus:ring-opacity-50 border p-2 text-sm"
                                                value={formData.role}
                                                onChange={e => setFormData({...formData, role: e.target.value})}
                                            >
                                                <option value="TEACHER">Docente</option>
                                                <option value="ADMIN">Admin (Coordinador)</option>
                                                <option value="SUPERADMIN">Superadmin</option>
                                            </select>
                                        </div>
                                    </div>
                                    <div className="mt-5 sm:mt-6 sm:grid sm:grid-flow-row-dense sm:grid-cols-2 sm:gap-3">
                                        <button
                                            type="submit"
                                            disabled={loading}
                                            className="inline-flex w-full justify-center rounded-md bg-upds-main px-3 py-2 text-sm font-semibold text-white shadow-sm hover:bg-blue-900 sm:col-start-2 disabled:opacity-50"
                                        >
                                            {loading ? 'Guardando...' : 'Guardar Usuario'}
                                        </button>
                                        <button
                                            type="button"
                                            className="mt-3 inline-flex w-full justify-center rounded-md bg-white px-3 py-2 text-sm font-semibold text-gray-900 shadow-sm ring-1 ring-inset ring-gray-300 hover:bg-gray-50 sm:col-start-1 sm:mt-0"
                                            onClick={onClose}
                                        >
                                            Cancelar
                                        </button>
                                    </div>
                                </form>
                           </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}
